<template>
  <div class="Collection" @mouseenter="setHovering(true)" @mouseleave="setHovering(false)">

    <div class="collectionHeader">
      <EditableInput v-model="collection.data.title" tag="h3" @change="updateTitle($event)"></EditableInput>
      <!--{{collection.rid}}-->

      <button @click="removeCollection" title="Ta bort samling">
        🗑️
      </button>

    </div>

    <div class="collectionContent" ref="collectionContent">
      <transition-group name="fade" mode="out-in" class="cards">
        <card v-for="(card,index) in cards" :card="card" @removeCard="removeCard" :key="card.rid" :id="card.rid"  :prid="collection.rid" tabindex="0"></card>
      </transition-group>

    </div>

    <div class="collectionFooter">
      <button class="fullWidth" @click="createCard" title="Lägg till nytt kort">Lägg till nytt kort</button>
    </div>


  </div>
</template>

<script>

import { store } from '@/store';
import Card from './Card'
import EditableInput from '@/components/general/EditableInput'

export default {
  name: 'Collection',
  components: {
    Card,
    EditableInput
  },
  data () {
    return {
      hovering: false,
      //cards: ""
    }
  },
  computed: {
    cards: function () {
      return this.collection.cards;
    },
    cardsFiltered: function () {
      return this.cards//.filter(card => card.states.selected != false || card.states.selectedConnected != false)
    }
  },
  props: {
    collection: "",
    prid: ""
  },
  watch: {
    /*collection: function () {
      this.getCardsFromAPI()
    }*/
  },
  methods: {
    setHovering: function (status){
      this.hovering = status;
    },
    removeCollection: function () {
      this.$emit('removeCollection',this.collection)
    },
    getCardsFromAPI: function () {
      console.log("Get cards")
      let params = {
        //endpoint: wallId + "/wall",
        endpoint: 'collections/' + this.collection.rid + "/cards",
        successCallback: (data) => {
          console.log(data)
          //this.cards = data.data;
          store.commit('setCollectionCards', {collection: this.collection, cards: data.data});
        },
      }
      store.commit('makeAPICall', params);
    },
    createCard: function () {
      //store.commit('createCardInCollection',{collection: this.collection});
      let params = {
        endpoint: "collections/" + this.collection.rid + "/cards",
        data: {title: 'Nytt kort'},
        method: "post",
        successCallback: (data) => {
          console.log(data)
          this.cards.push(data.data)
        },
      }

      store.commit('makeAPICall',params);
    },
    removeCard: function (card) {
      //store.commit('removeCardFromCollection',{collection: this.collection, card: card});
      let params = {
        endpoint: "collections/" + this.collection.rid + "/cards/" + card.rid,
        method: "delete",
        successCallback: (data) => {
          console.log(data)

          let index = this.collection.cards.indexOf(card)
          if(index != -1){
            this.collection.cards.splice(index,1)
          }

        },
      }

      store.commit('makeAPICall',params);
    },
    updateTitle: function (title) {
      let params = {
        endpoint: "walls/" + this.prid + "/collections/" + this.collection.rid,
        data: {title: title},
        method: "put",
        successCallback: (data) => {
          console.log(data.data)
        },
      }

      store.commit('makeAPICall',params);
    },
    handleScroll: function () {
      store.commit('setDirtyDraw');
    }
  },
  created: function () {
    store.commit('initCollection',this.collection);
  },
  mounted: function () {
    this.getCardsFromAPI();

    this.$refs.collectionContent.addEventListener('scroll', this.handleScroll);


  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.Collection{

  /* LAYOUT */
  display: flex;
  flex-direction: column;

  border: 1px solid #EAEAEA;
  background: #CADBDA;
  /*position: relative;*/
}

.Collection:nth-child(even){
  background: #ACCECC;
}

.Collection:hover{

}

.collectionHeader{
  padding: 0 20px 0 20px;
  border-bottom: 1px solid white;
}

.collectionContent{
  padding: 20px;
  flex: 1;
  overflow-y: scroll;
}

.collectionFooter{
  padding: 20px;
  border-top: 1px solid white;
}

.cards{
  display: flex;
  flex-direction: column;
  position: relative;
}

.remove{
  position: absolute;
  top: 5px;
  right: 5px;
}
</style>



